@import url('https://fonts.googleapis.com/css2?family=Anek+Malayalam:wght@100;200;300;400;700&display=swap');
*{
    margin: 0;
    padding: 0;
}
.App{
    font-family: 'Anek Malayalam', sans-serif;
}

.welcome{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    position: relative;
}
.name{
    color:black;
    font-size: 6rem;
    font-weight: 400;
    animation: name 1000ms 0s linear;
    position: absolute;
    top: 28%;
}
.my-name{
    color: orangered
}
.desc{
    font-size: 1.5rem;
    animation: description 1000ms .5s forwards;
    opacity: 0;
}

@keyframes name {
    0%{
        margin-left: 1800px;
        opacity: 0%;
        font-size: 1rem;
    }
    50%{
        margin-left: 900px;
        opacity: 0%;
        font-size: 2rem;
    }
    100%{
        margin-left: 0px;
        opacity: 100%;
        font-size: 6rem;
    }
}
@keyframes description {
    0%{
        margin-right: 1800px;
        opacity: 0%;
    }
    75%{
        opacity: 0%;
    }
    100%{
        margin-right: 0px;
        opacity: 100%;
        font-size: 1.5rem;
    }
}



.about{
    background-color: rgb(2, 2, 53);
    height: 100vh;
    color: white;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.col-one{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.about-header{
    font-size: 4rem;
    font-weight: 400;
    margin-bottom: 5%;
    color: orangered;
}
.about-desc{
    width: 70%;
    font-size: 2rem;
    line-height: 3rem;
    text-indent: 1rem;
}

.col-two{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 5rem;
}

.about-img{
    width: 25vw;
    height: 25vw;
    border: 4px solid orangered;
    border-radius: 50%;
}



.skills{
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 100vh;
    background-color: white;
}
.icon-list{
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
    gap: 5rem;
    height: 65%;
    margin: auto auto;
}
.icons{
    font-size: 8rem;
    color: rgb(2, 2, 53);
}

.list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 75%;
    margin: auto auto;
}
.skills-header{
    color: orangered;
    font-weight: 500;
    font-size: 4rem;
    width: 110%;
}
.skills-list{
    list-style: square;
    font-size: 2rem;
    line-height: 5rem;
}
.gitLink{
    color: orangered;
    text-decoration: none;
}



.projects{
    height: 100vh;
    background-color: rgb(2, 2, 53);
    display: flex;
    flex-direction: column;
    align-items: center;
}
.projects-header{
    color: orangered;
    font-weight: 500;
    font-size: 4rem;
    margin-top: 2.5%;
}
.project-section{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 50%;
    margin-top: 4rem;
}
.project-container{
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
}
.project-title{
    color:white;
    margin-bottom: 1rem;
}
.project-image{
    width:75%;
    height: 80%;
    object-fit: auto;
    border: 4px solid orangered;
}
.project-desc{
    color: White;
    font-size: 1.5rem;
    margin-top: 1rem;
    width: 75%;
    text-align: center;
}
.live-project{
    color: orangered;
    text-decoration: none;
    font-size: 1.5rem;
}
.project-code{
    color: yellow;
    text-decoration: none;
    font-size: 1.5rem;
}
.live-project:hover, .project-code:hover{
    color: aqua;
}
.links-div{
    display: flex;
    width: 75%;
    justify-content: center;
    justify-content: space-around;
    margin-top: 1rem;
}

@media  screen and (max-width:1440px) {
    .skills-header{
        font-size: 4rem;
        width: 110%;
    }
    .skills-list{
        list-style: square;
        font-size: 1.5rem;
        line-height: 4rem;
        font-weight: 600;
    }
}

@media  screen and (max-width:1140px) {
    .skills-header{
        font-size: 4rem;
        width: 110%;
    }
    .skills-list{
        list-style: square;
        font-size: 1.25rem;
        line-height: 3.5rem;
        font-weight: 600;
    }
    .about-desc{
        width: 70%;
        font-size: 1.75rem;
        line-height: 3rem;
        text-indent: 1rem;
    }
}

@media  screen and (max-width:960px) {
    .skills-header{
        font-size: 3.5rem;
        width: 110%;
    }
    .skills-list{
        list-style: square;
        font-size: 1.25rem;
        line-height: 3rem;
        font-weight: 600;
    }
    .about-desc{
        width: 70%;
        font-size: 1.75rem;
        line-height: 3rem;
        text-indent: 1rem;
    }
}

@media screen and (max-width:960px) {
    .project-section{
        display: flex;
        flex-direction: column;
    }
    .projects{
        height:auto;
    }
    .project-image{
        height: 50vw;
        width: 60vw;
    }
    .project-container{
        margin-top: 3rem;
    }
    .projects-header{
        font-size: 7vw;
        margin-bottom: -4rem;
        margin-top: 4rem;
    }
}
@media screen and (max-width:850px){
    .about{
        display: flex;
        flex-direction: column;
        height: auto;
    }
    .about-header{
        margin-bottom: 0;
        margin-top: 2rem;
    }
    .about-img{
        margin-top: 2rem;
        border-radius: 15%;
        height: 50vw;
        width: 50vw;
    }
}
@media screen and (max-width:760px){
    .name{
        font-size: 4rem;
    }
    .desc{
        font-size: .75rem;
        margin-top: -5rem;
    }
    .about-desc{
        font-size: 1.5rem;
    }
}
@media screen and (max-width:670px){
    .icon-list{
        display: none;
    }
    .skills{
        display: flex;
    }
    .skills-header{
        margin: 0 auto;
    }
    .about-desc{
        font-size: 1.75rem;
        line-height: normal;
        width: 90%;
    }
}