.Navbar{
    font-size: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    position: sticky;
    top: 0;
    opacity: 0;
    animation: nav-intro 2000ms 2s forwards;
    display: flex;
    text-align: center;
}
.nav-list{
    margin-left: 10%;
    text-align: center;
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 40%;
    border-radius: 100vw;
    padding-left: 1rem;
    padding-right: 1rem;
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
    font-weight: 300;
}
.nav-links{
    color: white;
    font-weight: 300;
    text-decoration: none;
}
.nav-links:hover{
    color: orangered
}

@keyframes nav-intro {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 100%;
    }
}

@media screen and (max-width:980px){
    .nav-links{
        font-size: 1.5rem;
    }
}
@media screen and (max-width:770px){
    .nav-links{
        font-size: 1rem;
    }
}
@media screen and (max-width:550px){
    .Navbar{
        display: none;
    }
}